import * as React from 'react';
import { FA } from 'tonva';
import { TaskCommonType } from '../../taskCommonType';
import { UiSchema, UiInputItem, UiRadio } from 'tonva';

export const Signback: TaskCommonType = {
    caption: '',
    icon: <FA name='shopping-bag' size="lg" fixWidth={true} />,
    schema: [
        { name: 'priorty', type: 'number', required: false },
        { name: 'deadline', type: 'string', required: false },
        { name: 'description', type: 'string', required: false },
    ],
    uiSchema: {
        items: {
            priorty: { widget: 'radio', label: '', defaultValue: 0, list: [{ value: 0, title: '' }, { value: 1, title: '' }] } as UiRadio,
            deadline: { widget: 'date', label: '', placeholder: '' } as UiInputItem,
            description: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            submit: { widget: 'button', label: '', }
        }
    } as UiSchema,
    completSchema: [
        { name: 'result', type: 'string', required: false },
    ],
    completuiSchema: {
        items: {
            result: { widget: 'radio', label: '', defaultValue: 1, list: [{ value: 0, title: '' }, { value: 1, title: '' }, { value: 1, title: '' }, { value: 1, title: '' }, { value: 1, title: '' }] } as UiRadio,
            submit: { widget: 'button', label: '', }
        }
    } as UiSchema
}
