import * as React from 'react';
import { FA } from 'tonva';
import { TaskCommonType } from '../../taskCommonType';
import { UiSchema, UiCheckItem, UiInputItem, UiRadio } from 'tonva';

export const NewCustomer: TaskCommonType = {
    caption: '',
    icon: <FA name='address-book' size="lg" fixWidth={true} />,
    schema: [
        { name: 'description', type: 'string', required: false },
        { name: 'priorty', type: 'number', required: false },
        { name: 'deadline', type: 'string', required: false },
    ],
    uiSchema: {
        items: {
            description: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            priorty: { widget: 'checkbox', label: '', placeholder: '' } as UiCheckItem,
            deadline: { widget: 'date', label: '', placeholder: '' } as UiInputItem,
            submit: { widget: 'button', label: '', },

            description2: { widget: 'radio', label: '2', defaultValue: 1, list: [{ value: 0, title: '' }, { value: 1, title: '' }] } as UiRadio,
        }
    } as UiSchema,
    completSchema: [
        { name: 'name', type: 'string', required: false },
        { name: 'telephone', type: 'number', required: false },
        { name: 'email', type: 'string', required: false },
        { name: 'wechat', type: 'string', required: false },
        { name: 'teacher', type: 'string', required: false },
        { name: 'potential', type: 'string', required: false },
        { name: 'research', type: 'string', required: false },
    ],
    completuiSchema: {
        items: {
            name: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            telephone: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            email: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            wechat: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            teacher: { widget: 'text', label: '', placeholder: '' } as UiInputItem,
            potential: { widget: 'radio', label: '', defaultValue: 1, list: [{ value: 0, title: '10' }, { value: 1, title: '10-30' }, { value: 2, title: '30' }] } as UiRadio,
            research: { widget: 'radio', label: '', defaultValue: 1, list: [{ value: 0, title: '' }, { value: 1, title: '' }, { value: 1, title: '' }, { value: 1, title: '' }] } as UiRadio,
            submit: { widget: 'button', label: '', },
        }
    } as UiSchema
}
